@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

* {
  margin: 0;
  padding: 0;
  text-decoration: inherit;
  color: inherit;
  font-family: 'Ubuntu', sans-serif;
}

ul {
  list-style: none;
}

body {
  background-color: #dee2ff;
  color: #8e9aaf;
}

nav {
  display: flex;
  padding: 12px;
  border: solid 1px #cbc0d3;
  justify-content: center;
  align-items: center;
}

.nav-list {
  display: flex;
  flex: 1;
  font-size: 16px;
  justify-content: space-around;
  align-items: center;
}

.nav-link {
  color: #8e9aaf;
}

.nav-link:hover {
  color: #cbc0d3;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  display: flex;
  margin: 16px;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  flex: 1;
}

.user-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  flex: 1;
  padding: 64px 32px;
  border: solid 2px #cbc0d3;
  border-radius: 4px;
  background-color: #cbc0d381;
  margin: 24px 0;
  max-width: 400px;
}

.input-field {
  padding: 4px 8px;
  color: #8e9aaf;
  outline: none;
  border: solid 1px #cbc0d3;
  border-radius: 4px;
}

.input-field:focus {
  outline: solid 1px #ffb3c6;
}

.form-btn {
  width: fit-content;
  padding: 4px 8px;
  border: solid 1px #ffc2d1;
  border-radius: 4px;
  background-color: #feeafa;
}

.form-btn:hover {
  background-color: #feeafab3;
}

.link {
  border: none;
  background: none;
  font-size: inherit;
  font-family: inherit;
}

.active {
  color: #e54b4b;
}

.photos-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  flex-direction: column;
  gap: 16px;
  flex: 1;
}

.photo-card {
  background-color: #feeafa;
  border: solid 1px #efd3d7;
  border-radius: 16px;
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

*:has(> .photo-img) {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo-img {
  object-fit: cover;
  background-color: red;
  flex: 1;
  width: 100%;
}

.icon-btn {
  background: none;
  border: none;
  display: flex;
  padding: 0;
  margin: 0;
}

.icon-btn > svg {
  height: 24px;
  width: 24px;
}

.icon-btn:hover {
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 0 4px 4px #dee2ff;
}

.control-container {
  display: flex;
  justify-content: left;
  align-items: center;
  gap: 16px;
}

.interaction-counts {
  font-size: 12px;
}

.profile-img {
  width: 50px;
  border-radius: 27px;
  border: solid 2px #dee2ff;
}

.post-header {
  display: flex;
  align-items: center;
  gap: 16px;
}

.comment-form {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

textarea {
  resize: vertical;
  min-height: 50px;
}

.comments-container {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.photo-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  flex: 1;
}

.comment-interact-container {
  font-size: 14px;
  color: #156064;
}

.comment-interact {
  cursor: pointer;
}

.comment-interact:hover {
  color: #cbc0d3;
}

.comment-container {
  color: #156064;
  display: flex;
  flex-direction: column;
  gap: 4px;
  background-color: #efd3d7;
  padding: 16px;
  border-radius: 16px;
}

.replies-container {
  padding-left: 32px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.caption {
  flex: 1;
}

@media only screen and (min-width: 600px) {
  main {
    padding-left: 40px;
    padding-right: 40px;
  }

  .photo-container {
    flex-direction: row;
  }

  .comments-container {
    flex: 1;
    overflow-y: auto;
    height: 80vh;
  }
}

@media only screen and (min-width: 992px) {
  main {
    padding-left: 100px;
    padding-right: 100px;
  }
}

@media only screen and (min-width: 1200px) {
  main {
    padding-left: 150px;
    padding-right: 150px;
  }
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #feeafa;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #8e9aaf;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2e294e;
}
